import { isTMA } from '@telegram-apps/sdk-react';

interface UserData {
  id: number;
  first_name: string;
  last_name?: string;
  username?: string;
  photo_url?: string;
}

//     Telegram WebApp
declare global {
  interface Window {
    Telegram?: {
      WebApp?: {
        initDataUnsafe: {
          user?: {
            id: number;
            first_name: string;
            last_name?: string;
            username?: string;
            photo_url?: string;
          };
        };
        initData: string;
        ready: () => void;
        MainButton: {
          text: string;
          isVisible: boolean;
          onClick: (callback: () => void) => void;
          show: () => void;
          hide: () => void;
        };
      };
    };
  }
}

/**
 *     Telegram WebApp
 */
export const getUserData = async (): Promise<UserData> => {
  try {
    //       
    console.log(' Telegram :', Boolean(window.Telegram));
    console.log(' WebApp :', Boolean(window.Telegram?.WebApp));
    
    // ,     Telegram
    const isTelegram = await isTMA('any'); //   'any'    
    console.log('isTMA :', isTelegram);
    
    //   isTMA  false,    
    const telegramWebApp = window.Telegram?.WebApp;
    
    if (telegramWebApp && telegramWebApp.initDataUnsafe && telegramWebApp.initDataUnsafe.user) {
      const user = telegramWebApp.initDataUnsafe.user;
      console.log('    Telegram:', user);
      
      //    Denis Usov
      if (user.first_name === 'Denis') {
        return {
          id: user.id || 1, //  ID 1   
          first_name: user.first_name,
          last_name: user.last_name,
          username: user.username,
          photo_url: user.photo_url
        };
      }
      
      return {
        id: user.id,
        first_name: user.first_name,
        last_name: user.last_name,
        username: user.username,
        photo_url: user.photo_url
      };
    }
    
    //    Telegram    ,     
    console.log('  Denis Usov  -');
    return {
      id: 1,
      first_name: 'Denis',
      last_name: 'Usov',
      username: 'denisusov'
    };
  } catch (error) {
    console.error('Error getting user data:', error);
    //        
    return {
      id: 1,
      first_name: 'Denis',
      last_name: 'Usov',
      username: 'denisusov'
    };
  }
};

